#projects {
  background: #f2f2f2; }

.projects-container {
  display: -ms-grid;
  display: grid;
  width: 90%;
  height: auto;
  margin: 0 auto;
  margin: 50px;
  -ms-grid-column-align: center;
  justify-items: center;
  -ms-grid-columns: [first] 1fr [second] 1fr [third] 1fr;
  grid-template-columns: [first] 1fr [second] 1fr [third] 1fr;
  -ms-grid-rows: [first] 18rem [second] 18rem;
  grid-template-rows: [first] 18rem [second] 18rem; }

@media (max-width: 1250px) {
  .projects-container {
    width: 90%;
    height: auto;
    -ms-grid-columns: [first] 1fr [second] 1fr;
    grid-template-columns: [first] 1fr [second] 1fr;
    -ms-grid-rows: [first] 16rem [second] 16rem [third] 16rem;
    grid-template-rows: [first] 16rem [second] 16rem [third] 16rem;
    -webkit-transition: 0.4s;
    transition: 0.4s; } }

@media (max-width: 800px) {
  .projects-container {
    width: 450px;
    height: auto;
    -ms-grid-columns: [first] 1fr;
    grid-template-columns: [first] 1fr;
    -ms-grid-rows: [first] 16rem [second] 16rem [third] 16rem [fourth] 16rem [fifth] 16rem [sixth] 16rem;
    grid-template-rows: [first] 16rem [second] 16rem [third] 16rem [fourth] 16rem [fifth] 16rem [sixth] 16rem;
    -webkit-transition: 0.4s;
    transition: 0.4s; } }

@media (max-width: 375px) {
  .projects-container {
    width: auto;
    height: auto;
    -webkit-transition: 0.4s;
    transition: 0.4s; } }

.project-grid {
  border-radius: 20px;
  filter: progid:DXImageTransform.Microsoft.Blur(pixelradius=1) progid:DXImageTransform.Microsoft.Light();
  width: 300px;
  height: 250px;
  -o-object-fit: cover;
  object-fit: cover;
  -webkit-transition: 0.2s;
  transition: 0.4s; }

@media (max-width: 1250px) {
  .project-grid {
    height: 15rem; } }

@media (max-width: 800px) {
  .project-grid {
    width: 400px; } }

@media (max-width: 375px) {
  .project-grid {
    width: 250px;
    object-fit: cover;
    margin: 0; } }

.project-grid:hover {
  cursor: pointer; }

.project-grid:hover:after {
  content: "Hello";
  color: red; }

.img__wrap {
  position: relative;
  width: 300px;
  height: 250px;
  border-radius: 20px;
  transition: 0.125s; }

.img__description {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.6);
  color: #fff;
  border-radius: 20px;
  visibility: hidden;
  opacity: 0;
  transition: 0.125s; }

.img__wrap:hover .img__description {
  visibility: visible;
  opacity: 1;
  transition: 0.125s; }
